.search {
    background-color: var(--white);
    width: 100%;
    max-width: 1440px;
    /* height: 150px; */
    top: 0;
    margin-bottom: 5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.5rem 1.3rem;
    margin-top: -4rem;
    border-radius: .5rem;
}

.tag__wraper {
    /* border: 1px solid red; */
    height: 2.3rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.tag__close {
    background-color: var(--desaturatedDarkCyan);
    border: none;
    height: 100%;
    padding: .5rem;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
    transition: .3s ease all;
}
.tag__close:hover{
    background-color: var(--veryDarkGrayishCyan);
}

.search__button {
    border: none;
    background: none;
    color: var(--darkGrayishCyan);
    font-size: 1rem;
    font-weight: 700;
    font-family: 'Spartan', sans-serif;
    cursor: pointer;
    transition: .3s ease all;
    border: 2px solid transparent;
}

.search__button:hover {
    color: var(--desaturatedDarkCyan);
    border-bottom: 2px solid var(--desaturatedDarkCyan);
}


.card {
    /* border: 1px solid red; */
    max-width: 1440px;
    width: 100%;
    height: auto;
    border-radius: .5rem;
    background-color: var(--white);
    display: flex;
    flex-direction: column;
    padding: 2.5rem 1.3rem 1.3rem;
    position: relative;
    box-shadow: 0px 13px 31px -20px rgba(91, 164, 164, 1);
    margin-top: 3rem;
}

.card--sale {
    border-left: 5px solid var(--desaturatedDarkCyan);
}

.card__logo {
    position: absolute;
    width: 3.5rem;
    top: -1.8rem;
}

.card__logo__image {
    width: 100%;
    vertical-align: top;
}

.card__info__title {
    font-size: .8rem;
    color: var(--desaturatedDarkCyan);
    display: inline-block;
    margin-right: 1rem;
}

.attribute {
    background-color: var(--veryDarkGrayishCyan);
    color: var(--lightGrayishCyanTablets);
    text-transform: uppercase;
    font-size: .6rem;
    padding: .5rem .6rem;
    border-radius: 1rem;
    letter-spacing: 1px;
    margin: .3rem;
    font-weight: 700;
}

.attribute--new {
    background-color: var(--desaturatedDarkCyan);
}

.card__info__job {
    font-size: 1rem;
    color: var(--veryDarkGrayishCyan);
    margin: 1rem 0;
    cursor: pointer;
    transition: .3s ease all;
}

.card__info__job:hover {
    color: var(--desaturatedDarkCyan);
}

.card__info__description {
    border-bottom: 1px solid rgba(123, 142, 142, 0.424);
    margin-bottom: 1.5rem;
}

.description {
    color: var(--darkGrayishCyan);
    font-size: .8rem;
    margin: 0 1rem;
    position: relative;
    display: inline-block;
    padding-bottom: 1rem;
}

.description::after {
    content: '';
    width: 4px;
    height: 4px;
    border-radius: 50%;
    display: block;
    position: absolute;
    top: 4px;
    left: -1rem;
    background-color: var(--darkGrayishCyan);
}

.description--one::after {
    display: none;
}

.description--one {
    margin-left: 0;
}

.card__filters {
    /* padding-top: 1.5rem; */
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;

}

.tag {
    padding: 9px;
    border-radius: 5px;
    /* margin: 1rem 1rem 0 0; */
    color: var(--desaturatedDarkCyan);
    font-weight: 700;
    font-size: .8rem;
    background-color: var(--lightGrayishCyanTablets);
    transition: .3s ease all;
    height: 2.3rem
}

.tag--card:hover {
    cursor: pointer;
    color: var(--white);
    background-color: var(--desaturatedDarkCyan);
}

.tag--search {
    border-radius: 5px 0 0 5px;
}

@media screen and (min-width:700px) {
    .card {
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding-bottom: 1.8rem;
        padding: 2rem;
        gap:2rem
    }
    .card__logo {
        position: relative;
        width: 6rem;
        top:0;
    }
    .card__info__description {
        border-bottom: none;
        margin-bottom: 0;
    }
    .card__wrap{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 1.2rem;
    }
    .description {
        padding-bottom: 0;
    }
}